
import { computed, defineComponent, onMounted, PropType, reactive, ref, toRefs, watch } from "vue";
import useDragMove from "@/hooks/useDragMove";
type Placement = "right" | "center" | "left";
export default defineComponent({
	emits: ["update:visible", "confirm", "cancel"],
	props: {
		visible: {
			type: Boolean,
			default: true,
		},
		title: {
			type: String,
			default: "",
		},
		okText: {
			type: String,
			default: "确定",
		},
		cancelText: {
			type: String,
			default: "取消",
		},
		bodyStyle: {
			type: String,
			default: "",
		},
		bodyClass: {
			type: String,
			default: "",
		},
		cancelable: {
			type: Boolean,
			default: false,
		},
		placement: {
			type: String as PropType<Placement>,
			default: "right",
		},
		contentStyle: {
			type: [String, Array, Object],
			default: "",
		},
		contentClass: {
			type: [String, Array, Object],
			default: "",
		},
	},
	setup(props, context) {
		const state = reactive({
			modalVisible: computed({
				get: () => props.visible,
				set: (newV) => context.emit("update:visible", newV),
			}),
			canRender: false,
			placementClass: computed(() => {
				if (props.placement === "left") {
					return "start-0";
				}
				if (props.placement === "center") {
					return "start-50 translate-x--50";
				}
				if (props.placement === "right") {
					return "end-0";
				}
			}),
		});
		const modalRef = ref(null);
		const dragRef = ref(null);
		onMounted(() => {
			state.canRender = true; //teleport标签必须检测到to指定的元素已经存在才能渲染
		});
		watch(
			() => state.modalVisible,
			(newV) => {
				if (newV) {
					useDragMove(modalRef, dragRef);
				}
			}
		);
		const clickCancel = () => {
			state.modalVisible = false;
			context.emit("cancel");
		};
		return {
			...toRefs(state),
			modalRef,
			dragRef,
			console,
			clickCancel,
		};
	},
});
