
import store from "@/store";
import { defineComponent, reactive, toRefs } from "vue";
export default defineComponent({
	props: {
		src: {
			type: String,
			required: true,
		},
		size: {
			type: [String, Number],
		},
		bodyClass: String,
	},
	setup() {
		const state = reactive({
			loaded: false,
			defaultArtistCover: store.state.defaultMusicCover,
		});
		return {
			...toRefs(state),
		};
	},
});
