
import { defineComponent, PropType, reactive, toRefs, watch } from "vue";
import { useStore } from "vuex";
export default defineComponent({
	props: {
		columns: {
			type: [String, Number],
			default: 5,
		},
		picUrl: {
			type: String,
		},
		name: {
			type: String,
		},
		toDetail: {
			type: Boolean,
			default: false,
		},
		id: {
			type: [String, Number],
		},
	},
	setup(props) {
		const store = useStore();
		const state = reactive({
			loaded: false,
			defaultMusicCover: store.state.defaultMusicCover,
		});
		const loadImage = (url) =>
			new Promise((resolve, reject) => {
				let image = new Image();
				image.onload = () => {
					resolve(url);
					image = undefined;
				};
				image.onerror = () => reject(url);
				image.src = url;
			});
		watch(
			() => props.picUrl,
			() => {
				state.loaded = false;
				loadImage(props.picUrl).then((res) => {
					state.loaded = true;
				});
			},
			{ immediate: true }
		);
		return {
			...toRefs(state),
		};
	},
});
